import React, { Component } from "react";

export default class Todo extends Component {
  state = {
    todoText: "",
    todoArr: []
  };
  onChange = e => {
    this.setState({
      todoText: e.target.value
    });
  };
  addTodo = () => {
    const { todoText, todoArr } = this.state;
    this.setState({
      todoText: "",
      todoArr: [todoText, ...todoArr]
    });
  };
  delTodo = idx => {
    const { todoText, todoArr } = this.state;
    this.setState({
      todoArr: todoArr.filter((ele, index) => index != idx)
    });
  };
  render() {
    const { todoText, todoArr } = this.state;
    return (
      <div>
        <h1>Todo List</h1>
        <input type="text" onChange={this.onChange} value={todoText} />
        <button onClick={this.addTodo}>Submit</button>
        <ul>
          {todoArr.map((c, idx) => (
            <li onClick={() => this.delTodo(idx)}>{c}</li>
          ))}
        </ul>
      </div>
    );
  }
}
